import React, { useState, useEffect } from "react";
import {
  CWidgetDropdown,
  CRow,
  CCol,
  CDropdown,
  CDropdownMenu,
  CDropdownItem,
  CDropdownToggle
} from '@coreui/react';
import CIcon from '@coreui/icons-react';
import ChartLineSimple from '../charts/ChartLineSimple';
import ChartBarSimple from '../charts/ChartBarSimple';
import * as productActions from "../../actions/Products/products";
import * as cashSalesActions from "../../actions/CashSale/cashSale";
import * as creditSalesActions from "../../actions/CreditSale/creditSale";
import * as TCActions from "../../actions/Trucks_Cadetes/Trucks_Cadetes";
import * as Eactions from "../../actions/Expenses/expenses";
import { withStyles } from "@material-ui/core";
import { connect } from "react-redux";
import { creditSale } from "src/reducers/CreditSale/creditSale";

var dateRange = "";
  var today = new Date();
  var dd = String(today.getDate()).padStart(2, '0');
  var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
  var yyyy = today.getFullYear();

  today = yyyy + '-' + mm + '-' + dd;

const styles = theme => ({
  root: {
    "& .MuiTableCell-head": {
      fontSize: "0.95rem"
    }
  },
  paper: {
    margin: theme.spacing(-8.9),
    padding: theme.spacing(6)
  },

  grid: {
    paddingLeft: theme.spacing(2)
  }
})



const WidgetsDropdown = ({ classes, ...props }) => {

  useEffect(() => {

    // props.fetchAllProducts();
    // props.fetchAllNSTransactions();
    // props.fetchAllTCTransactions();
    // props.fetchAllTransactionDetails();
    // props.fetchAllCSTransactionDetails();
    // props.fetchAllExpenses();
    // props.fetchAllCSTransactions();



  }, [])
//console.log(today);
try{
  var nSSalesList = props.salesList.filter(x => x.dateIn.toString().substring(0, 10) ==  today) ;
  var nSSales = (nSSalesList.reduce((nSSales, currentItem) => nSSales = nSSales + currentItem.totalPrice, 0));
  var cSSalesListPaid = props.creditSalesList.filter(x => x.dateIn.toString().substring(0, 10) == today);
  var cSSalesList = props.creditSalesList.filter(x => x.datePaid.toString().substring(0, 10) == today);
  var creditSales = (cSSalesListPaid.reduce((creditSales, currentItem) => creditSales = creditSales + currentItem.totalPrice, 0));
  var paidCreditSales = (cSSalesList.reduce((paidCreditSales, currentItem) => paidCreditSales = paidCreditSales + currentItem.paid , 0));
  var depo = (nSSalesList.reduce((depo, currentItem) => depo = depo + currentItem.gDeposit, 0));
  var cSDepo = (cSSalesList.reduce((cSDepo, currentItem) => cSDepo = cSDepo + currentItem.gDeposit, 0));
  var cadeteSalesList = props.tcTransactionsList.filter(x => x.returnDate.toString().substring(0, 10) == today);
  var cadeteSales = ( cadeteSalesList.reduce((cTDepo, currentItem) => cTDepo = cTDepo + currentItem.returnedTotalPrice, 0));
  var cTDepo = ( cadeteSalesList.reduce((cTDepo, currentItem) => cTDepo = cTDepo + currentItem.returnedGDeposit, 0));
  var totalDepo = depo + cSDepo + cTDepo;
  paidCreditSales = paidCreditSales - cSDepo;

  nSSales = nSSales + cadeteSales;

}catch{
  
}
  

  // render
  return (
    <CRow>
      <CCol sm="6" lg="3">
        <CWidgetDropdown
          color="gradient-primary"
          header={nSSales}
          text="Investors"
          footerSlot={
            <ChartLineSimple
              pointed
              className="c-chart-wrapper mt-3 mx-3"
              style={{ height: '70px' }}
              dataPoints={[65, 59, 84, 84, 51, 55, 40]}
              pointHoverBackgroundColor="primary"
              label="Sales"
              labels="months"
            />
          }
        >
          <CDropdown>
            <CDropdownToggle color="transparent">
              <CIcon name="cil-settings" />
            </CDropdownToggle>
            <CDropdownMenu className="pt-0" placement="bottom-end">
              <CDropdownItem>Action</CDropdownItem>
              <CDropdownItem>Another action</CDropdownItem>
              <CDropdownItem>Something else here...</CDropdownItem>
              <CDropdownItem disabled>Disabled action</CDropdownItem>
            </CDropdownMenu>
          </CDropdown>
        </CWidgetDropdown>
      </CCol>

      <CCol sm="6" lg="3">
        <CWidgetDropdown
          color="gradient-info"
          header={creditSales}
          text="Portfolios"
          footerSlot={
            <ChartLineSimple
              pointed
              className="mt-3 mx-3"
              style={{ height: '70px' }}
              dataPoints={[1, 18, 9, 17, 34, 22, 11]}
              pointHoverBackgroundColor="info"
              options={{ elements: { line: { tension: 0.00001 } } }}
              label="Credit Sales"
              labels="months"
            />
          }
        >
          <CDropdown>
            <CDropdownToggle caret={false} color="transparent">
              <CIcon name="cil-location-pin" />
            </CDropdownToggle>
            <CDropdownMenu className="pt-0" placement="bottom-end">
              <CDropdownItem>Action</CDropdownItem>
              <CDropdownItem>Another action</CDropdownItem>
              <CDropdownItem>Something else here...</CDropdownItem>
              <CDropdownItem disabled>Disabled action</CDropdownItem>
            </CDropdownMenu>
          </CDropdown>
        </CWidgetDropdown>
      </CCol>

      <CCol sm="6" lg="3">
        <CWidgetDropdown
          color="gradient-warning"
          header={paidCreditSales}
          text="Users"
          footerSlot={
            <ChartLineSimple
              className="mt-3"
              style={{ height: '70px' }}
              backgroundColor="rgba(255,255,255,.2)"
              dataPoints={[78, 81, 80, 45, 34, 12, 40]}
              options={{ elements: { line: { borderWidth: 2.5 } } }}
              pointHoverBackgroundColor="warning"
              label="Paid Credit Sales"
              labels="months"
            />
          }
        >
          <CDropdown>
            <CDropdownToggle color="transparent">
              <CIcon name="cil-settings" />
            </CDropdownToggle>
            <CDropdownMenu className="pt-0" placement="bottom-end">
              <CDropdownItem>Action</CDropdownItem>
              <CDropdownItem>Another action</CDropdownItem>
              <CDropdownItem>Something else here...</CDropdownItem>
              <CDropdownItem disabled>Disabled action</CDropdownItem>
            </CDropdownMenu>
          </CDropdown>
        </CWidgetDropdown>
      </CCol>

      <CCol sm="6" lg="3">
        <CWidgetDropdown
          color="gradient-danger"
          header={totalDepo}
          text="Patners"
          footerSlot={
            <ChartBarSimple
              className="mt-3 mx-3"
              style={{ height: '70px' }}
              backgroundColor="rgb(250, 152, 152)"
              label="Members"
              labels="months"
            />
          }
        >
          <CDropdown>
            <CDropdownToggle caret className="text-white" color="transparent">
              <CIcon name="cil-settings" />
            </CDropdownToggle>
            <CDropdownMenu className="pt-0" placement="bottom-end">
              <CDropdownItem>Action</CDropdownItem>
              <CDropdownItem>Another action</CDropdownItem>
              <CDropdownItem>Something else here...</CDropdownItem>
              <CDropdownItem disabled>Disabled action</CDropdownItem>
            </CDropdownMenu>
          </CDropdown>
        </CWidgetDropdown>
      </CCol>
    </CRow>
  )
}

const mapStateToProps = state => ({
  productsList: state.products.list,
  creditSalesList: state.creditSale.list,
  tcTransactionsList: state.trucks_cadetes.list,
  nSTransactionDeatail: state.cash.list,
  salesList: state.cashSale.list,
  cSaleDetailsList: state.credit.list,
  tcTransDetailsList: state.trucks.list,
  expensesList: state.expenses.list
})

const mapActionToProps = {

  fetchAllProducts: productActions.fetchAllProduct,
  fetchAllNSTransactions: cashSalesActions.fetchAllTransactions,
  fetchAllTCTransactions: TCActions.fetchAllTransactions,
  fetchAllCSTransactions: creditSalesActions.fetchAllTransactions,
  fetchAllTransactionDetails: cashSalesActions.fetchAllTransactionDetails,
  fetchAllCSTransactionDetails: creditSalesActions.fetchAllTransactionDetails,
  fetchAllTCTransactionDetails: TCActions.fetchAllTransactionDetails,
  fetchAllExpenses: Eactions.fetchAllExpenses,
}


export default connect(mapStateToProps, mapActionToProps)(withStyles(styles)(WidgetsDropdown));
